import { useState, useEffect, useRef } from 'react'
import { getLocale } from '../utils'

function getVoice(voices) {
  const locale = getLocale()
  const preferredVoice = voices.find((voice) => voice.name === locale.voice)

  // seems like voice locale can be either en-GB or en_GB
  return preferredVoice
    ? preferredVoice
    : voices.find(
        (voice) =>
          voice.lang.replace(/[^a-zA-Z]/gi, '') ===
          locale.voiceLanguage.replace(/[^a-zA-Z]/gi, '')
      )
}

function useSpeech(text, onStart, onEnd) {
  const [voicesLoaded, setVoicesLoaded] = useState(false)
  const savedStartCallback = useRef()
  const savedEndCallback = useRef()

  // save callbacks
  useEffect(() => {
    savedStartCallback.current = onStart
    savedEndCallback.current = onEnd
  }, [onStart, onEnd])

  useEffect(() => {
    const synth = window.speechSynthesis
    const voices = synth.getVoices()

    function handleVoiceChange() {
      setVoicesLoaded(true)
    }

    // need to load the voices
    if (!voices.length) {
      synth.addEventListener('voiceschanged', handleVoiceChange)
    } else {
      const utter = new SpeechSynthesisUtterance()
      utter.text = text
      utter.lang = getLocale().voiceLanguage
      utter.voice = getVoice(voices)
      utter.onstart = savedStartCallback.current
      utter.onend = savedEndCallback.current

      synth.speak(utter)
    }

    return () => {
      synth.cancel()
      synth.removeEventListener('voiceschanged', handleVoiceChange)
    }
  }, [text, voicesLoaded])
}

export default useSpeech
